/* Vesting.css */

.vesting-container {
  max-width: 1200px;
  margin: 1rem auto 2rem;
  padding: 1rem;
  background: linear-gradient(to bottom right, #111322, #1e2235);
  border-radius: 1.0rem;
  box-shadow: 0 0 15px rgba(138, 43, 226, 0.2);
  color: #f9f9f9;
}

.vesting-container h1 {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #c084fc;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.connect-btn {
  padding: 0.7rem 1.4rem;
  background: linear-gradient(to right, #7c3aed, #4f46e5);
  border: none;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.connect-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 8px rgba(124, 58, 237, 0.5);
}

.vesting-summary {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
  margin-bottom: 2.5rem;
  text-align: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.vesting-summary h2 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: #9ca3af;
}

.vesting-summary p {
  font-size: 1.2rem;
  font-weight: bold;
  color: #fdfdfd;
}

/* New chart layout for equal height and alignment */
.vesting-charts {
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
}

.vesting-charts > div {
  flex: 1 1 20%;
  min-width: 280px;
  min-height: 340px;
  background-color: #1e213a;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(100, 100, 255, 0.05);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.vesting-charts h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #e5e7eb;
}

/* Table styles */
.vesting-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #16192b;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 8px rgba(147, 51, 234, 0.2);
}

.vesting-table th,
.vesting-table td {
  padding: 0.9rem 1.2rem;
  border-bottom: 1px solid #272b42;
  text-align: center;
}

.vesting-table th {
  background-color: #2e314c;
  color: #c084fc;
  font-weight: 600;
}

.vesting-table td {
  color: #f1f1f1;
}

.vesting-table .claim-btn {
  padding: 0.5rem 1rem;
  border: none;
  background-color: #8b5cf6;
  color: white;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s ease;
}

.vesting-table .claim-btn:hover {
  background-color: #7c3aed;
  box-shadow: 0 0 5px rgba(139, 92, 246, 0.4);
}

@media (max-width: 768px) {
  .vesting-summary {
    flex-direction: column;
    align-items: center;
  }

  .vesting-charts {
    flex-direction: column;
    align-items: center;
  }

  .vesting-container {
    padding: 1.2rem;
  }

  .vesting-charts > div {
    width: 100%;
    min-width: unset;
  }
}

.vesting-intro {
  font-size: 1.05rem;
  font-weight: 500;
  color: #928b8b;
  margin-top: 0.75rem;
  margin-bottom: 1.75rem;
  line-height: 1.7;
}

.vesting-charts > div svg {
  margin: auto;
}

@media (max-width: 768px) {
  .vesting-container h1 {
    font-size: 1.5rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .connect-btn {
    width: 100%;
    font-size: 1rem;
    padding: 0.8rem;
  }

  .vesting-summary div {
    width: 100%;
    margin-bottom: 1rem;
  }

  .vesting-table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
    border-radius: 0.75rem;
  }

  .vesting-table th,
  .vesting-table td {
    padding: 0.75rem;
    font-size: 0.9rem;
  }

  .vesting-table .claim-btn {
    padding: 0.6rem 1rem;
    font-size: 0.95rem;
    width: 100%;
  }

  .vesting-intro {
    font-size: 0.95rem;
    line-height: 1.6;
  }

  .vesting-charts > div {
    padding: 1rem;
    min-height: 300px;
  }

  .vesting-charts h3 {
    text-align: center;
  }

  .vesting-charts > div svg {
    max-width: 100%;
  }
}
