.presale {
  max-width: 600px;
  margin: 4rem auto;
  padding: 2rem;
  background: linear-gradient(to bottom, #141829, #0f1122);
  border-radius: 1.5rem;
  box-shadow: 0 0 25px rgba(128, 90, 213, 0.4);
  color: white;
  font-family: 'Segoe UI', sans-serif;
  text-align: center;
}

.presale h2 {
  font-size: 2rem;
  font-weight: 800;
  color: #c084fc;
  margin-bottom: 1rem;
}

.price {
  font-size: 1.25rem;
  color: #4ade80;
  margin-bottom: 0.5rem;
}

.countdown {
  margin-bottom: 1rem;
  color: #e0e0e0;
  font-size: 0.95rem;
}

/* 🔥 Updated Progressbar */
.progressbar {
  height: 20px;
  background: #2d2d45;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 1rem;
  position: relative;
  box-shadow: inset 0 0 5px #00000066;
}

.progress {
  height: 100%;
  background: linear-gradient(to right, #9333ea, #7c3aed);
  transition: width 0.5s ease-in-out;
  border-radius: 10px 0 0 10px;
  text-align: right;
  padding-right: 10px;
  font-size: 0.85rem;
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.info p {
  margin: 0.2rem 0;
  color: #d4d4d4;
  font-size: 0.95rem;
}

.token-buttons {
  display: flex;
  justify-content: space-between;
  margin: 1.5rem 0;
}

.token-buttons button {
  flex: 1;
  margin: 0 0.4rem;
  padding: 0.6rem 1rem;
  background: #181c2f;
  border: 1px solid #555;
  color: #fff;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: background 0.3s;
}

.token-buttons button.active {
  background: #9333ea;
  box-shadow: 0 0 10px #9333ea80;
}

.token-icon {
  width: 20px;
  height: 20px;
}

.input-section {
  text-align: left;
  margin-bottom: 1rem;
}

.input-section label {
  display: block;
  margin-bottom: 0.4rem;
  font-size: 0.9rem;
  font-weight: 500;
  color: #cbd5e1;
}

.input-section input {
  width: 100%;
  padding: 0.75rem;
  border-radius: 10px;
  background: #1e223a;
  color: #fff;
  border: 1px solid #3b3b5f;
  font-size: 1rem;
}

.buy-btn {
  background: linear-gradient(to right, #9333ea, #7c3aed);
  color: white;
  padding: 0.8rem;
  width: 100%;
  margin-top: 1.2rem;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.buy-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 15px #7c3aed80;
}

.wallet-icon {
  width: 20px;
  height: 20px;
}

.confirmation-box {
  background-color: #2c1a47;
  color: #90ee90;
  border: 1px solid #764ba2;
  padding: 16px;
  margin-bottom: 20px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 0 12px #764ba280;
}
.confirmation-box a {
  color: #66ccff;
  text-decoration: underline;
}

.presale-wrapper {
  display: flex;
  flex-wrap: wrap; /* 🛠️ allow wrapping */
  justify-content: center;
  align-items: flex-start;
  margin-top: 1rem;
  padding-bottom: 4rem;
  gap: 1rem; /* 🛠️ smaller gap */
  max-width: 900px; /* 🛠️ constrain max width */
  margin-left: auto;
  margin-right: auto;
}



.presale-info-box {
  align-self: center;
  background: #121228;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 0 30px rgba(0,0,0,0.3);
  width: 360px;
  flex: 1 1 360px;
  max-width: 360px;
}

.presale-info-box h3 {
  font-size: 1.4rem;
  color: #c084fc;
  margin-bottom: 1rem;
}

.presale-info-box ul {
  padding-left: 1.2rem;
  font-size: 0.95rem;
  line-height: 1.6;
}

.presale-info-box ul li::before {
  content: "✔";
  color: #7c3aed;
  margin-left: -1.2rem;
  margin-right: 0.6rem;
}

.presale {
  background: #121228;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 0 30px rgba(0,0,0,0.3);
  width: 440px;
  flex: 1 1 400px;
  max-width: 440px;
}

.notice {
  margin-top: 1.5rem;
  font-size: 0.9rem;
  background-color: #2c1a47;
  border-left: 4px solid #9333ea;
  padding: 0.8rem;
  border-radius: 10px;
  color: #b0b0ff;
}

@media (max-width: 768px) {
  .presale-wrapper {
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    gap: 2rem;
  }
}


.custom-list {
  list-style: none;
  padding-left: 0;
  margin: 1rem 0;
}

.custom-list li {
  margin-bottom: 0.75rem;
  font-size: 1rem;
  color: #f3f4f6;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

@media (max-width: 768px) {
  .presale-wrapper {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    padding: 1rem;
  }

  .presale-info-box,
  .presale {
    width: 100% !important;
    max-width: 100% !important;
    padding: 1.5rem;
    box-sizing: border-box;
  }

  .token-buttons {
    flex-direction: column;
    align-items: stretch;
    gap: 0.75rem;
  }

  .token-buttons button {
    width: 100%;
    justify-content: center;
    font-size: 1rem;
    padding: 0.8rem 1rem;
  }

  .input-section input {
    font-size: 1rem;
    padding: 0.8rem;
  }

  .buy-btn {
    font-size: 1rem;
    padding: 1rem;
  }

  .confirmation-box {
    font-size: 0.9rem;
    padding: 1rem;
  }

  .notice {
    font-size: 0.85rem;
    padding: 1rem;
  }

  .presale-info-box ul {
    font-size: 0.9rem;
    line-height: 1.5;
  }
}

.mobile-warning {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  background: #facc15;
  color: #1f1f1f;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  margin: 1rem auto;
  max-width: 700px;
  font-size: 0.95rem;
  font-weight: 500;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  position: relative;
}

.mobile-warning .icon {
  color: #92400e;
}

.mobile-warning button {
  background: none;
  border: none;
  font-size: 1rem;
  margin-left: auto;
  color: #333;
  cursor: pointer;
}
