.admin-burn-container {
  max-width: 1440px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #0d0b20;
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.admin-burn-container h2 {
  font-size: 1.8rem;
  color: #f97316;
  margin-bottom: 1.5rem;
}

.admin-burn-form {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  background-color: #1e1b36;
  padding: 1.5rem;
  border-radius: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  align-items: center;
}

.admin-burn-form input,
.admin-burn-form select {
  padding: 0.9rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 10px;
  background-color: #2e2c4d;
  color: #ffffff;
  flex: 1 1 200px;
  min-width: 180px;
  max-width: 240px;
}

.admin-burn-form input[type="datetime-local"] {
  padding: 0.8rem;
  color-scheme: dark;
}

.admin-burn-form input::placeholder {
  color: #a1a1aa;
}

.admin-burn-form select option {
  background-color: #1f1b3a;
  color: #fff;
}

.admin-burn-form button {
  background-color: #f97316;
  color: #ffffff;
  font-weight: bold;
  padding: 0.9rem 1.5rem;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  flex: 1 1 160px;
  max-width: 180px;
}

.admin-burn-form button:hover {
  background-color: #fb923c;
}

@media (max-width: 768px) {
  .admin-burn-form {
    flex-direction: column;
    align-items: stretch;
  }

  .admin-burn-form input,
  .admin-burn-form select,
  .admin-burn-form button {
    width: 100%;
  }
}

.status-banner {
  padding: 10px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: bold;
  border-radius: 5px;
}

.success {
  background-color: #d1fae5;
  color: #065f46;
}

.error {
  background-color: #fee2e2;
  color: #991b1b;
}

@media (max-width: 768px) {
  .admin-burn-container h2 {
    font-size: 1.5rem;
    text-align: center;
    line-height: 1.4;
  }

  .form-group {
    width: 100%;
    margin-bottom: 1rem;
  }

  .admin-burn-form input,
  .admin-burn-form select {
    font-size: 1rem;
    padding: 0.9rem;
  }

  .admin-burn-form button {
    padding: 1rem;
    font-size: 1rem;
    margin-top: 1rem;
  }

  .status-banner {
    font-size: 0.95rem;
    padding: 12px;
  }

  .auth-error {
    text-align: center;
    font-size: 1rem;
    color: #f87171;
  }

  .spinner {
    margin-right: 0.5rem;
  }
}
