/* StrategicVesting.css */

.strategic-vesting-container {
    max-width: 1200px;
    margin: 3rem auto 4rem;
    padding: 2rem;
    background: linear-gradient(135deg, #121429, #1f223e);
    border-radius: 1.8rem;
    box-shadow: 0 0 25px rgba(124, 58, 237, 0.35), 0 0 15px rgba(59, 130, 246, 0.25);
    color: #e0e7ff;
  }
  
  .strategic-vesting-container h1 {
    font-size: 2.2rem;
    margin-bottom: 2rem;
    color: #818cf8;
    text-align: center;
  }
  
  .connect-btn {
    padding: 0.8rem 1.6rem;
    background: linear-gradient(to right, #6366f1, #8b5cf6);
    border: none;
    border-radius: 12px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    display: block;
    margin: 1.5rem auto;
  }
  
  .connect-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 0 12px rgba(99, 102, 241, 0.6);
  }
  
  .vesting-summary {
    display: flex;
    justify-content: space-around;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    text-align: center;
    gap: 2rem;
    flex-wrap: wrap;
  }
  
  .vesting-summary h2 {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    color: #a5b4fc;
  }
  
  .vesting-summary p {
    font-size: 1.3rem;
    font-weight: bold;
    color: #e0e7ff;
  }
  
  /* 🔥 New Fixed Layout for Charts */
  .vesting-charts-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin-bottom: 2.5rem;
  }
  
  .chart-item {
    background: radial-gradient(circle at top left, #272b5b, #202446);
    padding: 1.4rem;
    border-radius: 16px;
    box-shadow: 0 0 18px rgba(67, 56, 202, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 350px;
    align-items: center; 
    justify-content: center;
  }
  
  .chart-item h3 {
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
    color: #dbeafe;
    text-align: center;
  }
  
  /* Table Styles */
  .vesting-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #1c1f3a;
    border-radius: 14px;
    overflow: hidden;
    box-shadow: 0 0 12px rgba(147, 51, 234, 0.25);
  }
  
  .vesting-table th,
  .vesting-table td {
    padding: 1rem 1.2rem;
    border-bottom: 1px solid #2d3354;
    text-align: center;
  }
  
  .vesting-table th {
    background: linear-gradient(to right, #6366f1, #8b5cf6);
    color: white;
    font-weight: bold;
  }
  
  .vesting-table td {
    color: #e2e8f0;
  }
  
  .vesting-table .claim-btn {
    padding: 0.6rem 1.2rem;
    border: none;
    background-color: #818cf8;
    color: white;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s ease;
  }
  
  .vesting-table .claim-btn:hover {
    background-color: #6366f1;
    box-shadow: 0 0 6px rgba(99, 102, 241, 0.6);
  }
  
  /* Responsive for Mobile 📱 */
  @media (max-width: 768px) {
    .vesting-summary {
      flex-direction: column;
      align-items: center;
    }
  
    .vesting-charts-grid {
      grid-template-columns: 1fr;
    }
  
    .chart-item {
      width: 100%;
    }
  
    .strategic-vesting-container {
      padding: 1.5rem;
    }
  }
  
  .strategic-vesting-intro {
    font-size: 1.05rem;
    font-weight: 500;
    color: #837e7e;
    margin-top: 0.75rem;
    margin-bottom: 2rem;
    line-height: 1.7;
  }
  
  @media (max-width: 768px) {
    .strategic-vesting-container h1 {
      font-size: 1.6rem;
      text-align: center;
      line-height: 1.4;
    }
  
    .connect-btn {
      width: 100%;
      font-size: 1rem;
      padding: 1rem;
    }
  
    .vesting-table {
      display: block;
      overflow-x: auto;
      white-space: nowrap;
      font-size: 0.9rem;
      border-radius: 0.75rem;
    }
  
    .vesting-table th,
    .vesting-table td {
      padding: 0.75rem 1rem;
      font-size: 0.95rem;
    }
  
    .vesting-table .claim-btn {
      width: 100%;
      padding: 0.6rem;
      font-size: 0.95rem;
    }
  
    .strategic-vesting-intro {
      font-size: 0.95rem;
      line-height: 1.6;
      text-align: justify;
    }
  
    .chart-item {
      min-height: 320px;
      padding: 1rem;
    }
  }
  