.burning-dashboard {
  max-width: 1440px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #0d0b20;
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.dashboard-title {
  font-size: 2rem;
  color: #f97316;
  margin-bottom: 2rem;
  text-align: left;
}

.burning-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.burning-card {
  background-color: #1f1b3a;
  border-radius: 12px;
  padding: 1.5rem;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s ease;
}

.burning-card:hover {
  transform: translateY(-4px);
}

.burning-card h2 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  color: #c084fc;
}

.burning-card p {
  font-size: 1.6rem;
  font-weight: bold;
  color: #fff;
}

.burning-charts {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* two charts side by side */
  gap: 2rem;
  margin-bottom: 2rem;
}

.burning-charts-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 charts side by side */
  gap: 2rem;
  margin-bottom: 3rem;
}

.burning-chart {
  background-color: #1f1b3a;
  border-radius: 12px;
  padding: 1.5rem;
  min-height: 350px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.burn-table-title {
  font-size: 1.2rem;
  color: #fb923c;
  margin: 2rem 0 1rem;
}

.burning-table table {
  width: 100%;
  border-collapse: collapse;
  background-color: #2d2c4b;
  border-radius: 8px;
  overflow: hidden;
}

.burning-table th, .burning-table td {
  padding: 1rem;
  text-align: left;
}

.burning-table th {
  background-color: #3b3a5c;
  color: #c084fc;
  font-weight: bold;
}

.burning-table td {
  background-color: #2d2c4b;
}

.burning-table tr:nth-child(even) td {
  background-color: #322f4f;
}

@media (max-width: 1024px) {
  .burning-charts-grid {
    grid-template-columns: 1fr; /* stack vertically on mobile */
  }



  .burning-card,
  .burning-chart {
    width: 100%;
  }
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0,0,0,0.1);
  border-top-color: #8b5cf6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 20px auto;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.progress-bar {
  width: 100%;
  background-color: #e5e7eb;
  height: 10px;
  border-radius: 8px;
  overflow: hidden;
}

.progress-fill {
  height: 10px;
  background-color: #4ade80;
  transition: width 0.6s ease-in-out;
}

.error-message {
  color: red;
  text-align: center;
  margin-top: 20px;
  font-weight: bold;
}

.top-pools {
  list-style: none;
  padding-left: 0;
  text-align: center;
}

.top-pools li {
  margin: 5px 0;
}

.burning-intro {
  font-size: 1.05rem;
  font-weight: 500;
  color: #bbb9ab;
  margin-top: 0.75rem;
  margin-bottom: 1.75rem;
  line-height: 1.7;
}

@media (max-width: 768px) {
  .dashboard-title {
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 1.5rem;
  }

  .burning-card h2 {
    font-size: 1rem;
  }

  .burning-card p {
    font-size: 1.3rem;
  }

  .burning-table {
    overflow-x: auto;
    display: block;
    white-space: nowrap;
    border-radius: 0.75rem;
  }

  .burning-table table {
    min-width: 600px;
  }

  .burning-charts-grid {
    grid-template-columns: 1fr;
  }

  .burning-chart {
    padding: 1rem;
    min-height: 300px;
  }

  .progress-bar {
    height: 8px;
  }

  .progress-fill {
    height: 8px;
  }

  .top-pools {
    font-size: 0.95rem;
    padding: 0 0.5rem;
    text-align: center;
  }

  .burning-intro {
    font-size: 0.95rem;
    line-height: 1.6;
    text-align: justify;
  }

  .burning-stats {
    grid-template-columns: 1fr;
  }

  .burning-card {
    padding: 1rem;
  }
}
