.tokenomics {
    max-width: 1200px;
    margin: 4rem auto;
    padding: 2rem;
    background: linear-gradient(to bottom, #0f0c29, #302b63, #24243e);
    border-radius: 1.5rem;
    box-shadow: 0 0 25px rgba(128, 90, 213, 0.25);
    font-family: 'Segoe UI', sans-serif;
    color: white;
  }
  
  .tokenomics-title {
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 2rem;
    background: linear-gradient(to right, #9333ea, #7c3aed);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .tokenomics-boxes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1.5rem;
    margin-bottom: 3rem;
  }
  
  .token-box {
    flex: 1 1 45%;
    background: #1f2937;
    border-radius: 1rem;
    padding: 1.5rem;
    border: 1px solid #4b5563;
    box-shadow: 0 0 15px rgba(128, 90, 213, 0.1);
  }
  
  .token-box h3 {
    color: #c084fc;
    margin-bottom: 0.8rem;
  }
  
  .token-box ul {
    padding-left: 1rem;
    line-height: 1.8;
    color: #d1d5db;
  }
  
  .tokenomics-graphs {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    margin-bottom: 2rem;
  }
  
  .chart-container {
    flex: 1 1 400px;
    max-width: 500px;
    background: #111827;
    border-radius: 1.2rem;
    padding: 1.2rem;
    border: 1px solid #374151;
    box-shadow: 0 0 10px rgba(147, 51, 234, 0.15);
  }
  
  .total-supply {
    text-align: center;
    font-size: 1.2rem;
    margin: 2rem 0 1rem;
    color: #a5f3fc;
    font-weight: 500;
  }
  
  .vesting-table table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.95rem;
    margin-top: 1rem;
    background: #1e1e2f;
    border-radius: 0.5rem;
    overflow: hidden;
  }
  
  .vesting-table th,
  .vesting-table td {
    padding: 0.9rem 1.2rem;
    text-align: left;
    border: 1px solid #333;
  }
  
  .vesting-table th {
    background-color: #1f2937;
    color: #c084fc;
  }
  
  .vesting-table tr:nth-child(even) {
    background-color: #181c2f;
  }
  
  .vesting-table tr:hover {
    background-color: #2a2f45;
  }
  
  @media (max-width: 768px) {
    .tokenomics-boxes {
      flex-direction: column;
    }
  
    .tokenomics-graphs {
      flex-direction: column;
      align-items: center;
    }
  
    .chart-container {
      width: 100%;
    }
  
    .token-box {
      flex: 1 1 100%;
    }
  }
  
  @media (max-width: 768px) {
    .tokenomics-title {
      font-size: 1.8rem;
      text-align: center;
      line-height: 1.4;
    }
  
    .token-box h3 {
      font-size: 1.1rem;
      margin-bottom: 0.5rem;
    }
  
    .token-box ul {
      font-size: 0.9rem;
      padding-left: 0.5rem;
      line-height: 1.6;
    }
  
    .total-supply {
      font-size: 1rem;
      padding: 0 1rem;
    }
  
    .vesting-table table {
      font-size: 0.85rem;
      margin-top: 1.5rem;
    }
  
    .vesting-table th,
    .vesting-table td {
      padding: 0.7rem 1rem;
    }
  
    .chart-container {
      padding: 1rem;
    }
  
    .tokenomics {
      padding: 1rem;
    }
  }
  