/* --- Final MediCoin Staking Dashboard Styles --- */

.staking-dashboard {
  max-width: 1440px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #0d0b20;
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.staking-title {
  font-size: 2rem;
  color: #32e796;
  margin-bottom: 2rem;
  text-align: left;
}

.staking-section {
  margin-bottom: 3rem;
}

.staking-section-title {
  font-size: 1.5rem;
  color: #f97316;
  margin-bottom: 1.5rem;
}

.wallet-connect-banner,
.wallet-status-banner {
  background-color: #1f1b3a;
  padding: 1.2rem;
  border-radius: 12px;
  text-align: center;
  margin-bottom: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.connect-wallet-btn {
  background: linear-gradient(to right, #6366f1, #8b5cf6);
  color: #ffffff;
  font-weight: bold;
  padding: 0.7rem 1.5rem;
  border-radius: 10px;
  margin-top: 1rem;
  border: none;
  transition: 0.3s ease;
}

.connect-wallet-btn:hover {
  transform: scale(1.05);
  background: linear-gradient(to right, #8b5cf6, #6366f1);
}

.staking-table-wrapper {
  overflow-x: auto;
  background-color: #1f1b3a;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.staking-table {
  width: 100%;
  border-collapse: collapse;
}

.staking-table th, .staking-table td {
  padding: 1rem;
  text-align: center;
}

.staking-table th {
  background-color: #3b3a5c;
  color: #c084fc;
}

.staking-table td {
  background-color: #2d2c4b;
}

.staking-table tr:nth-child(even) td {
  background-color: #322f4f;
}

.staking-action-btn {
  background: linear-gradient(to right, #6366f1, #8b5cf6);
  color: #ffffff;
  font-weight: bold;
  padding: 0.7rem 1.5rem;
  border-radius: 10px;
  margin-top: 1rem;
  border: none;
  transition: 0.3s ease;
}

.staking-action-btn:hover {
  transform: scale(1.05);
  opacity: 0.9;
}

/* Profit Calculator */
.staking-calculator-wrapper {
  gap: 2rem;
}

.staking-calculator-card select,
.staking-calculator-card option {
  background-color: #1f1b3a; /* same dark background */
  color: #ffffff; /* white text */
  border: 1px solid #4f46e5; /* optional light purple border */
  padding: 0.5rem;
  border-radius: 8px;
}

.staking-calculator-card select:hover,
.staking-calculator-card option:hover {
  background-color: #312e81; /* darker on hover */
}

.staking-calculator-card input,
.staking-calculator-card select,
.staking-calculator-card textarea {
  background-color: #2d2c4b; /* dark input field */
  color: #ffffff; /* white text */
  border: 1px solid #3b3a5c;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  font-size: 1rem;
}

.staking-calculator-card input::placeholder {
  color: #aaa; /* lighter placeholder text */
}

.staking-calculator-card input:focus,
.staking-calculator-card select:focus,
.staking-calculator-card textarea:focus {
  outline: none;
  border-color: #8b5cf6; /* highlight border on focus */
  background-color: #2d2c4b; /* stay dark */
}

.confirm-stake-btn {
  background: linear-gradient(to right, #8b5cf6, #6366f1); /* purple gradient */
  color: white;
  font-weight: bold;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.confirm-stake-btn:hover {
  background: linear-gradient(to right, #a78bfa, #818cf8);
  transform: scale(1.05);
}

.staking-calculator-results {
  background-color: #2d2c4b;
  padding: 2rem;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  color: #e0e7ff;
}

.staking-calculator-info p {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.staking-calculator-note {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #9ca3af;
}

/* Charts Grid */
.staking-charts-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* exactly 2 charts side by side */
  gap: 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.chart-item {
  background-color: #1f1b3a;
  padding: 1.5rem;
  border-radius: 12px;
  min-height: 300px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.chart-title {
  font-size: 1.1rem;
  font-weight: bold;
  color: #4ade80;
  margin-bottom: 1rem;
  text-align: center;
}

/* For Mobile Screens */
@media (max-width: 1024px) {
  .staking-charts-grid {
    grid-template-columns: 1fr; /* one chart per row on mobile */
  }
}


/* User Stakes */
.user-stake-card {
  background-color: #1f1b3a;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 2rem;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.staking-stake-info p {
  margin-bottom: 0.5rem;
}

.staking-claim-btn {
  background: linear-gradient(to right, #846fd8, #7d45dd);
  font-weight: bold;
  border: none;
  border-radius: 10px;
}

.staking-claim-btn:hover {
  transform: scale(1.05);
}

.staking-early-btn {
  background: linear-gradient(to right, #b3a22f, #e3d27e);
  font-weight: bold;
  border: none;
  border-radius: 10px;
}

.staking-early-btn:hover {
  transform: scale(1.05);
}

/* Spinner */
.staking-loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.staking-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0,0,0,0.1);
  border-top-color: #8b5cf6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Responsive */
@media (max-width: 1024px) {
  .staking-charts-grid {
    grid-template-columns: 1fr;
  }
}

.staking-intro {
  font-size: 1.05rem;
  font-weight: 500;
  color: #787676;
  margin-top: 0.75rem;
  margin-bottom: 1.75rem;
  line-height: 1.7;
}

@media (max-width: 768px) {
  .staking-title {
    font-size: 1.6rem;
    text-align: center;
    line-height: 1.4;
  }

  .staking-intro {
    font-size: 0.95rem;
    line-height: 1.6;
    text-align: justify;
    padding: 0 0.5rem;
  }

  .wallet-status-banner p,
  .wallet-connect-banner p {
    font-size: 0.95rem;
  }

  .staking-section-title {
    font-size: 1.3rem;
    text-align: center;
  }

  .staking-table th,
  .staking-table td {
    font-size: 0.9rem;
    padding: 0.75rem;
  }

  .staking-action-btn,
  .connect-wallet-btn,
  .staking-claim-btn,
  .staking-early-btn {
    font-size: 0.9rem;
    padding: 0.6rem 1rem;
    width: 100%;
    margin-top: 0.5rem;
  }

  .staking-calculator-wrapper {
    flex-direction: column;
  }

  .staking-calculator-card,
  .staking-calculator-results {
    margin-top: 1rem;
    padding: 1rem;
  }

  .chart-title {
    font-size: 1rem;
    margin-bottom: 0.75rem;
  }

  .user-stake-card {
    margin-bottom: 1.5rem;
    padding: 1rem;
  }

  .staking-stake-info p {
    font-size: 0.95rem;
  }
}

.staking-charts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}
.chart-item {
  width: 100%;
  padding: 10px;
}